import React from 'react';
import { element, node, oneOf, string } from 'prop-types';
import classnames from 'classnames';
import Icon from '../icon';

const CSS_PREFIX = 'vds-progress-gauge';
const CSS_PREFIX_SPINNER = 'vds-progress--spinner';
const CSS_STATUS = 'vds-status';
const SIZES = {
  large: {
    backgroundStyle: {
      strokeDasharray: '255, 7',
      strokeDashoffset: '3'
    },
    barStyle: {
      strokeDasharray: '180, 180',
      strokeDashoffset: '101'
    },
    cx: 56,
    cy: 56,
    height: 112,
    r: 54,
    strokeWidth: 4,
    width: 112
  },
  small: {
    backgroundStyle: {
      strokeDasharray: '90, 8',
      strokeDashoffset: '50'
    },
    barStyle: {
      strokeDasharray: '135, 100',
      strokeDashoffset: '90'
    },
    cx: 32,
    cy: 32,
    height: 64,
    r: 30,
    strokeWidth: 4,
    width: 64
  }
};

const ProgressGauge = ({ children, className, icon, size, status, ...remainingProps }) => (
  <div
    className={classnames(CSS_PREFIX, {
      [`${CSS_STATUS}--${status}`]: status !== 'default',
      [`${CSS_STATUS}--${size}`]: size !== 'large'
    }, className)}
    {...remainingProps}
  >
    <div className={classnames(`${CSS_PREFIX}-display`)}>
      <div className={classnames(`${CSS_PREFIX}-ring`)}>
        <svg
          className={classnames({
            [`${CSS_PREFIX}-track`]: status !== 'progress',
            [`${CSS_PREFIX_SPINNER}-track`]: status === 'progress'
          })}
          height={SIZES[size].height}
          width={SIZES[size].width}
        >
          <circle
            className={classnames({
              [`${CSS_PREFIX}-background`]: status !== 'progress',
              [`${CSS_PREFIX_SPINNER}-background`]: status === 'progress'
            })}
            cx={SIZES[size].cx}
            cy={SIZES[size].cy}
            r={SIZES[size].r}
            strokeWidth={SIZES[size].strokeWidth}
            style={status === 'progress' ? { ...SIZES[size].backgroundStyle } : undefined}
          />
          {
            status === 'progress'
              ? <circle
                className={classnames(`${CSS_PREFIX_SPINNER}-bar`)}
                cx={SIZES[size].cx}
                cy={SIZES[size].cy}
                r={SIZES[size].r}
                strokeWidth={SIZES[size].strokeWidth}
                style={{ ...SIZES[size].barStyle }}
              />
              : undefined
          }
        </svg>
      </div>
      <div className={classnames(`${CSS_PREFIX}-icon`)}>
        {icon}
      </div>
    </div>
    {children}
  </div>
);

ProgressGauge.propTypes = {
  /**
   * @ignore
   */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * Icon
   */
  icon: element,

  /**
   * Size
   */
  size: oneOf(['large', 'small']),

  /**
   * Status
   */
  status: oneOf(['default', 'not-started', 'progress', 'completed', 'approved'])
};

ProgressGauge.defaultProps = {
  icon: <Icon name="cloud-upload" resolution="high" />,
  size: 'large',
  status: 'not-started'
};

ProgressGauge.displayName = 'ProgressGauge';

export default ProgressGauge;
