import React from 'react';
import { node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-progress-gauge-label';

const ProgressGaugeLabel = ({ children, className, ...remainingProps }) => (
  <div
    className={classnames(CSS_PREFIX, className)}
    {...remainingProps}
  >
    {children}
  </div>
);

ProgressGaugeLabel.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string
};

ProgressGaugeLabel.displayName = 'ProgressGaugeLabel';

export default ProgressGaugeLabel;
