import React from 'react';
import { node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-progress-gauge-step';

const ProgressGaugeStep = ({ children, className, ...remainingProps }) => (
  <div
    className={classnames(CSS_PREFIX, className)}
    {...remainingProps}
  >
    {children}
  </div>
);

ProgressGaugeStep.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string
};

ProgressGaugeStep.displayName = 'ProgressGaugeStep';

export default ProgressGaugeStep;
