import React, { useState } from 'react';
import Col from '../../col';
import Grid from '../../grid';
import Icon from '../../icon';
import ProgressGauge from '../../progress-gauge';
import ProgressGaugeDate from '../../progress-gauge-date';
import ProgressGaugeLabel from '../../progress-gauge-label';
import ProgressGaugeStep from '../../progress-gauge-step';
import Radio from '../../radio';
import Row from '../../row';
import Typography from '../../typography';

const ProgressGaugeExample = () => {
  const [state, setState] = useState({
    currentStep: 0,
    steps: [
      {
        date: 'September 8, 2019',
        icon: <Icon name="cloud-upload" resolution="high" />,
        label: 'Not Started',
        status: 'default',
        step: 'Application'
      },
      {
        date: 'November 8, 2019',
        icon: <Icon name="cloud-upload" resolution="high" />,
        label: 'In Progress',
        status: 'progress',
        step: 'Application'
      },
      {
        date: 'December 8, 2019',
        icon: <Icon name="security-protection" resolution="high" />,
        label: 'Completed',
        status: 'completed',
        step: 'Application'
      },
      {
        date: 'January 8, 2020',
        icon: <Icon name="checkmark" resolution="high" />,
        label: 'Approved',
        status: 'approved',
        step: 'Application'
      }
    ]
  });

  return (
    <Grid>
      <Row>
        <Col>
          <Typography tag="h2">Progress Gauge</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <form onChange={e => setState({ ...state, currentStep: e.target.value })}>
            {
              state.steps.map((step, key) => (
                <Radio
                  defaultChecked={state.currentStep === key}
                  key={key}
                  label={step.label}
                  name="step-example"
                  value={key}
                />
              ))
            }
          </form>
          <ProgressGauge
            icon={state.steps[state.currentStep].icon}
            status={state.steps[state.currentStep].status}
          >
            <React.Fragment>
              <ProgressGaugeStep>{state.steps[state.currentStep].step}</ProgressGaugeStep>
              <ProgressGaugeDate>{state.steps[state.currentStep].date}</ProgressGaugeDate>
              <ProgressGaugeLabel>{state.steps[state.currentStep].label}</ProgressGaugeLabel>
            </React.Fragment>
          </ProgressGauge>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Progress Gauge (small)</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <ProgressGauge
            icon={<Icon name="cloud-upload" resolution="low" />}
            size="small"
          >
            <React.Fragment>
              <ProgressGaugeStep>Application</ProgressGaugeStep>
              <ProgressGaugeDate>September 8, 2019</ProgressGaugeDate>
              <ProgressGaugeLabel>Default</ProgressGaugeLabel>
            </React.Fragment>
          </ProgressGauge>
        </Col>
      </Row>
    </Grid>
  );
};

export default ProgressGaugeExample;
